import React, {Component} from 'react';
import home from '../images/Web Dev Midterm Project-Page-1.png'
import cs from '../images/Web Dev Midterm Project-CS.png'
import activities from '../images/Web Dev Midterm Project-Activities.jpg'
import modeling from '../images/Web Dev Midterm Project-Modeling.png'
import sitemap from '../images/sitemap.PNG'


class Week7 extends Component{
    render(){
        return(
            <div>
                <h1>SiteMap</h1>
                <img src={sitemap}/>
                <h1>HomePage</h1>
                <img src={home}/>
                <h1>CS Stuff</h1>
                <img src={cs}/>
                <h1>Modeling</h1>
                <img src={modeling}/>
                <h1>Activities</h1>
                <img src={activities}/>
            </div>
        )
    }
}

export default Week7;